.inquiry-container {
	overflow-y: auto;
	padding-top: 60px;

	scrollbar-color: var(--general-2);
	scrollbar-width: thin;

	&::-webkit-scrollbar {
		width: 8px;
		height: 8px;
	}

	&::-webkit-scrollbar-thumb {
		border-radius: 100px;
		background: var(--general-2);
	}
}

.inquiry-header {
	height: 60px;
	max-width: 400px;
	z-index: 2;
}

.close-btn {
	width: 40px !important;
	height: 40px !important;
	box-shadow: unset !important;
}

.icon-20px {
	font-size: 20px;
	width: 20px;
	height: 20px;
}

.filter {
	border: 1px solid var(--prim-2);
}
